.App {
  text-align: center;
  height: 100%;
}

.App-header {
  background-color: rgba(255, 255, 255, 0.8);
  height: auto;
  position: absolute;
  width: 100%;
  z-index: 100;
}

.App-status-overlay {
  position: absolute;
  top: 80px;
  width: 100%;
  z-index: 100;
}

.feedback {
  background: white;
  bottom: 10px;
  color: #444;
  padding: 10px;
  position: fixed;
  right: 0;
}

.arrow {
  margin: 0 10px;
  color: #888;
}



